import React from 'react';
import './style.css';

const Stats = ({ stats }) => {
  if (!stats) {
    return <span className='stats'>Loading...</span>;
  }
  return (
    <span className='stats'>
      {stats.error && 'üòê Error!'}
      {stats.isLoading && 'üôÑ Loading...'}
      {stats.downloads !== null && `üòé ${stats.downloads}`}
    </span>
  );
};

export default Stats;
